.footer__content{
    background-color: rgb(12, 12, 12);
    height: 100px;
}

.name{
    margin-top: 10px;
    color: white;
    font-size: 1.3rem;
    font-weight: 400;
    text-align: center;
}

.name a{
    color: white;
    text-decoration: none;
}

.name a:hover{
    color: #f8312f;
}

.links{
    padding-top: 10px;
    text-align: center;
}

.links a{
    margin: 20px;
    font-size: 25px;
    color: white;
    text-decoration: none;
}

.links a:hover {
    color: #0a66c2;
}


.footer__fadeTop{
    height: 2rem;
    background-image: linear-gradient(180deg, transparent, #111, rgb(12, 12, 12));
}