.banner{
    /* line-height: 400px; */
    /* padding-top: 3rem; */
    color: white;
    object-fit: contain;
    height: 500px;
    margin-bottom: 20px;
}

.banner__contents{
    margin-left: 30px;
    padding-top: 250px; 
    height: 190px;
}

.banner__title{
    font-size: 3rem;
    font-weight: 800;
    margin-bottom: 0.3rem;
}

.banner__button{
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
 }

 .banner__button:hover{
    color:#000;
    background-color: #e6e6e6;
    transition: all 0.2s;
 }

.banner__description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;

}

.banner__fadeBottom{
    height: 3.8rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111);

}

