.row{
    color: white;
    margin-left: 20px;
}

.Posters{
    display: flex;
    overflow-y: hidden;
    padding: 20px;
}

.Posters::-webkit-scrollbar{
    display: none;
}
.row__poster__content{
    object-fit: contain;
    width: 100%;
    margin-right: 10px;
}

.row_poster{
    max-height: 100px;
    border-radius: 4px;
    cursor: pointer;
    transition: transform 400ms;
}

.row_poster:hover{
    transform: scale(1.08);
}

.row_bigposter{
    max-height: 250px;
}

.row__poster__title{
    font-weight: 500;
    display: flex;
    justify-content: center;
    font-size: 13px;
    margin-top: 10px;
}